@import 'Variables';

.photoList {
  width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-row-gap: 45px;
}
@media all and (min-width: 768px) {
  .photoList {
    margin-bottom: 100px;
    transform: scale(1.5);
  }
}

